<template>
  <div>
    <el-card class="wsy-card">
      <div class="el-card_body">
        <el-table :data="RightList" border stripe style="width: 100%">
          <el-table-column type="index" label="#"></el-table-column>
          <el-table-column prop="authName" label="权限名称"></el-table-column>
          <el-table-column prop="path" label="路径"></el-table-column>
          <el-table-column label="权限等级">
            <template slot-scope="scope">
              <div>
                <el-tag v-show="scope.row.level == 0">一级</el-tag>
                <el-tag type="success" v-show="scope.row.level == 1">二级</el-tag>
                <el-tag type="info" v-show="scope.row.level == 2">三级</el-tag>
                <el-tag type="warning" v-show="scope.row.level == 3">四级</el-tag>
                <el-tag type="danger" v-show="scope.row.level == 4">五级</el-tag>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      RightList: []
    };
  },
  created() {},
  mounted() {
    this.getRightList();
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    next();
  },
  methods: {
    // 获取权限表格数据
    getRightList() {
      this.$axios.get("rights/list").then(({ data }) => {
        console.log(data);
        this.RightList = data.data;
      });
    }
  },
  filters: {},
  computed: {},
  watch: {}
};
</script>

<style lang='scss' scoped>
.wsy-card {
  margin-top: 15px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15) !important;
  border: 1px solid #ebeef5;
  background-color: #fff;
  color: #303133;
  transition: 0.3s;
  border-radius: 4px;
  overflow: hidden;
  > .el-card_body {
    padding: 20px;
  }
}
.el-table {
  margin-top: 15px;
}
</style>
